:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(329, 70%, 58%);
  --instagram-via: hsl(5, 77%, 71%);
  --instagram-end: hsl(37, 97%, 70%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --toggle-shadow-dark: rgb(255 255 255 / 50%);
  --toggle-shadow-light: rgb(0 0 0 / 50%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 72em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 69.5rem;
  padding: 0 1.5rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 72em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3.625rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
  gap: 0.375rem 0.5rem;
  border: none;
  margin: 0;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem;
  opacity: 0;
  cursor: pointer;
  width: 1.25rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus-visible ~ .toggle__switcher {
  box-shadow: 0 0 0 0.5rem var(--toggle-shadow-dark);
}
.toggle label {
  font-size: 0.875rem;
  cursor: pointer;
  grid-row: 2/3;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 1/2;
  justify-self: center;
  align-self: end;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
  grid-column: 2/3;
  grid-row: 2/3;
}
.toggle__background {
  display: block;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 1.5rem;
  overflow: hidden;
  background-image: linear-gradient(135deg, var(--toggle-bg-start) 2%, var(--toggle-bg-end) 100%);
  pointer-events: none;
}
.toggle__switcher {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 0.2s ease-in-out;
}
.toggle #light:checked ~ .toggle__switcher {
  left: calc(100% - 1.3125rem);
  right: 0.1875rem;
}
.toggle #system:checked ~ .toggle__switcher {
  left: 50%;
  right: auto;
  transform: translateX(-50%);
}

.card-parent {
  display: grid;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .card-parent {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 72em) {
  .card-parent {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background-color: var(--dark-card);
  color: var(--dark-text1);
  text-align: center;
  padding: 1.5rem;
  border-top: 0.25rem solid transparent;
  border-radius: 0.3125rem;
  overflow: hidden;
}
.card--facebook {
  border-color: var(--facebook);
}
.card--twitter {
  border-color: var(--twitter);
}
.card--instagram {
  padding-top: 1.75rem;
  border: 0;
  position: relative;
}
.card--instagram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.1875rem;
  background-image: linear-gradient(225deg, var(--instagram-start) 0%, var(--instagram-via) 50%, var(--instagram-end) 100%);
}
.card--youtube {
  border-color: var(--youtube);
}
.card__platform {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  height: 1.25rem;
  margin-bottom: 1.75rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5rem;
}
.card__count {
  font-weight: 700;
  font-size: 3.5rem;
  letter-spacing: -0.125rem;
  color: var(--dark-text2);
  margin-bottom: 0.25rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
  color: var(--dark-text1);
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--up svg {
  fill: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change--down svg {
  fill: var(--brightred);
  transform: rotate(180deg);
}